.preloader {
  height: 100%;
  width: 100%;
  z-index: 1000;
  background-image: url("desk-header-2x.0842fa6d.jpg");
  background-size: cover;
  justify-content: center;
  align-items: center;
  transition: all .15s ease-out;
  display: flex;
  position: fixed;
}

.preloader--hide {
  opacity: 0;
  visibility: hidden;
  z-index: -1;
}

.loader {
  width: 100%;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: changeColor 1.5s linear infinite;
  display: flex;
  position: relative;
}

@keyframes changeColor {
  0% {
    filter: hue-rotate();
  }

  100% {
    filter: hue-rotate(360deg);
  }
}

.loader__div {
  display: flex;
  position: relative;
}

.loader__tittle {
  color: #00ff0a;
  letter-spacing: 12px;
  font-family: consolas;
  font-weight: 300;
}

.dot {
  width: 20px;
  height: 20px;
  animation: animateDot 1.2s linear infinite;
  animation-delay: calc(.1s * var(--i) );
  background: #00ff0a;
  border-radius: 50%;
  margin: 20px 10px;
  display: block;
  position: relative;
  transform: scale(.1);
  box-shadow: 0 0 10px #00ff0a, 0 0 20px #00ff0a, 0 0 40px #00ff0a, 0 0 60px #00ff0a, 0 0 80px #00ff0a, 0 0 110px #00ff0a;
}

@keyframes animateDot {
  0% {
    transform: scale(.1);
  }

  10% {
    transform: scale(1);
  }

  50%, 100% {
    transform: scale(.1);
  }
}

.spinner {
  width: 100vw;
  height: 100vh;
  background-color: inherit;
  visibility: hidden;
  z-index: 900;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.spinner--is-hidden {
  opacity: 0;
  visibility: hidden;
}

.spinner--is-active {
  visibility: visible;
}

.spinner:after {
  content: "";
  width: 95px;
  height: 95px;
  border: 15px solid #fff;
  border-top-color: #ff6b01;
  border-radius: 50%;
  animation: loading .3s infinite;
}

@keyframes loading {
  from {
    transform: rotate(0turn);
  }

  to {
    transform: rotate(1turn);
  }
}

